import {projeto} from '../class/_Projeto';

export default function InstalacaoEletrica() {
  const itensInclude = [
    'Calculo e dimensionamento das fiaes',
    'Distribuio dos pontos de iluminao',
    'Distribuio dos pontos de Tomada',
    'Lista de materiais',
    '02 pedidos de reviso',
  ];
  const itensExcluded = [
    'Acompanhamento da execuo',
    'Padro da concessionria',
  ];
  const observacao = [
    '',
  ];

  const arquitetonico = new projeto('Hidrossanitrio');
  arquitetonico.itensIncludeInsert(itensInclude);
  arquitetonico.itensExcludedInsert(itensExcluded);
  arquitetonico.observacaoInsert(observacao);
  return arquitetonico;
}
